<template>
  <div>
    <div class="clearfix">
      <el-button :plain="true" type="info" class="pull-right" @click="back">返回</el-button>
    </div>
  	<fullcalendar :events="fcEvents" lang="zh" v-on:dayClick="dayClick"></fullcalendar>
    <el-dialog title="SKU设置" v-model="dialogFormVisible">
      <el-form  :label-position="labelPosition" :model="skuForm" ref="skuForm" label-width="100px">
        <el-form-item label="选中日期">
        <el-input auto-complete="off" v-model="skuForm.date" disabled></el-input>
        </el-form-item>
        <el-form-item 
          label="设置价格"
          prop="price"
          :rules="[
            { required: true, message: '价格不能为空'},
            { type: 'number', message: '价格必须为数字值'}
          ]"
        >
          <el-input auto-complete="off" v-model.number="skuForm.price" placeholder="输入价格"></el-input>
        </el-form-item>
        <el-form-item 
          label="设置库存"
          prop="stock"
          :rules="[
            { required: true, message: '库存不能为空'},
            { type: 'number', message: '库存必须为数字值'}
          ]"
        >
          <el-input auto-complete="off" v-model.number="skuForm.stock" placeholder="输入库存"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('skuForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('skuForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { formatDate } from '@/assets/js/date.js'
export default {
  data () {
    return {
      fcEvents: [{
        title: '价格：5888 库存：100',
        start: '2017-04-25'
      },
      {
        title: '价格：5888 库存：100',
        start: '2017-04-26'
      }
      ],
      dialogFormVisible: false,
      labelPosition: 'right',
      skuForm: {
        date: '',
        price: '',
        stock: ''
      }
    }
  },
  methods: {
    dayClick (date, jsEvent) {
      this.skuForm.date = this.formatDate(date)
      this.dialogFormVisible = true
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.saveSku(this.skuForm)
          this.dialogFormVisible = false
          this.$refs[formName].resetFields()
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.dialogFormVisible = false
      this.$refs[formName].resetFields()
    },
    saveSku (formObj) {
      let event = {
        title: '价格：' + formObj.price + '库存' + formObj.stock,
        start: formObj.date
      }
      this.fcEvents.push(event)
      this.$message({
        type: 'success',
        message: '设置成功！'
      })
    },
    formatDate (time) {
      var date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd')
    },
    back () {
      history.go(-1)
    }
  },
  filters: {
    formatDate (time) {
      var date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd')
    }
  }
}
</script>
<style>
  .comp-full-calendar {
    max-width: 100%;
  }
  .clearfix {
    margin-bottom: 15px
  }
  .full-calendar-header .header-center span{
    font-family: sans-serif;
    font-size: 20px;
    font-weight: bold
  }
  .full-calendar-body .weeks {
    height: 30px;
    line-height: 30px
  }
</style>



